<!-- 成员管理的标签列表 -->
<template>
  <div class="log_root">

    <div class="date_picker_container">
      <el-date-picker
        v-model="time"
        type="daterange"
        align="right"
        unlink-panels
        range-separator="至"
        start-placeholder="开始日期"
        end-placeholder="终止日期"
        @change="dateChange"
        :picker-options="pickerOptions2"
        :default-time="['00:00:00', '23:59:59']">
      </el-date-picker>

      <el-input class="keyword" placeholder="关键字" v-model="keyword"></el-input>
      <el-button type="primary" @click="search">查询</el-button>
    </div>

    <!-- 购买记录列表 -->
    <div class="container borderRadius">
      <el-table class="logs_table" :data="logs" border style="width:100%" ref="logsTable">
        <el-table-column prop="params" label="关键字" align="center"></el-table-column>
        <el-table-column prop="typeModule" label="查询模块" align="center"></el-table-column>
        <el-table-column prop="total" label="数据信息总计" align="center"></el-table-column>
        <el-table-column prop="ip" label="IP地址" align="center"></el-table-column>
        <el-table-column prop="accessTime" label="查询时间" align="center"></el-table-column>
      </el-table>

      <!-- 翻页 -->
      <div class="pagination">
        <el-pagination @current-change="handleCurrentChange" background :page-size="pageSize" layout="prev, pager, next" :total="totalRowNum"></el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
  import moment from 'moment'
  export default {
    data() {
      return {
        logs: [
          {
            id: '0',
            ip: '',
            status: '',
            browser: '',
            os: '',
            currentTime: '',
          }
        ],
        page: 1,
        pageSize: 10,
        totalRowNum: 0,
        pickerOptions2: {
          shortcuts: [{
            text: '最近一周',
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7)
              picker.$emit('pick', [start, end])
            }
          }, {
            text: '最近一个月',
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30)
              picker.$emit('pick', [start, end])
            }
          }, {
            text: '最近三个月',
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90)
              picker.$emit('pick', [start, end])
            }
          }]
        },
        defaultDate: [moment().format('YYYY-MM-01 HH:mm:ss'), moment().format('YYYY-MM-01 HH:mm:ss')],
        time: '',
        keyword: '',
      }
    },
    methods: {
      dateChange(times) {
        let sdate = times[0]
        let edate = times[1]
        let stime = moment(sdate).format('YYYY-MM-DD HH:mm:ss')
        let etime = moment(edate).format('YYYY-MM-DD HH:mm:ss')

        this.time = [stime, etime]
        this.getUserLoginLogs(this.time)
      },

      handleCurrentChange (val) {
        this.page = val

        this.getUserLoginLogs(this.time)
      },

      getUserLoginLogs (times) {

        let startTime = times && times.length > 0 ? moment(times[0]).format('YYYY-MM-DD HH:mm:ss') : moment().format('YYYY-MM-DD HH:mm:ss')
        let endTime = times && times.length > 1 ? moment(times[1]).format('YYYY-MM-DD HH:mm:ss') : moment().format('YYYY-MM-DD HH:mm:ss')

        console.log("startTime:" + startTime)
        console.log("endTime:" + endTime)

        let params = {
          page: this.page,
          pageSize: this.pageSize,
          startTime: startTime,
          endTime: endTime,
          params: this.keyword,
        }
        console.log('----------------')
        console.log(params)
        gapi.getUserSearchLogs(params)
          .then((res) => {
            console.log(res)
            if (res.data.code === 1) {
              this.totalRowNum = res.data.data.data.totalRowNum
              this.logs = res.data.data.data.resultList
              console.log(this.totalRowNum)
              console.log(this.logs)
            } else {
              return false
            }
          }).catch((err) => {
            console.log(err)
        })
      },

      // 获得月的时间范围
      // 【每月第一天, 今天]
      monthTimes() {
        let startTime = moment().format('YYYY-MM-01 HH:mm:ss')
        let endTime = moment().format('YYYY-MM-DD HH:mm:ss')
        return [startTime, endTime]
      },

      search() {
        this.getUserLoginLogs(this.time)
      }
    },

    created() {
      this.time = this.monthTimes()
      console.log("this.time:")
      console.log(this.time)
      this.getUserLoginLogs(this.time)
    }
  }
</script>

<style>
  .date_picker_container {
    margin: 10px;
  }

  .log_root {
    margin: 0 auto;
  }

  .log_root .borderRadius {
    box-shadow: 0 0 10px 2px rgba(6,0,1,0.1);
    border-radius: 10px;
    padding: 0;
  }

  .log_root .borderRadius .logs_table {
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    border: none;
  }

  .log_root .el-table__header .cell {
    background: rgba(7,134,223,1);
    color: rgba(255,255,255,1);
    font-size: 16px;
    font-weight: normal;
  }

  .keyword {
    width: 150px;
  }


</style>



