<!-- 成员管理的标签列表 -->
<template>
  <div>
    <button
      v-for="(data, index) in datas"
      :class="['button-base', index === selIndex ? 'selected' : 'unselected']"
      @click="onClicked(data, index)">
        <span :class="['text', index === selIndex? 'selected-text' : 'unselected-text']">{{ data.name }}</span>
    </button>
  </div>
</template>

<script>
    export default {
      name: 'ButtonTab',
      props: {
        clicked: {
          type: Function,
          default: (index, data) => {}
        },
        selectedIndex: {
          type: Number,
          default: 0
        },
        buttons: {
          type: Array,
          default: [
            {
              index: 0,
              name: '标签1',
            },
            {
              index: 1,
              name: '标签2',
            }
          ]
        }
      },
      data() {
        return {
          datas: this.buttons,
          selIndex: this.selectedIndex || 0
        }
      },
      methods: {
        onClicked(data, index) {
          this.selIndex = index
          this.clicked(data, index)
        }
      },

      created() {
        if (this.datas && this.datas.length > 0)
          this.clicked(this.datas[this.selIndex], this.selIndex)
      }
    }
</script>

<style scoped>
  .button-base {
    width: 150px;
    height: 40px;
    margin: 0px 10px 10px 0px;
    border-radius: 5px;
    box-shadow: 0 0 10px rgba(6,0,1,0.1);
  }

  .selected {
    background: #4778C7;
  }

  .unselected {
    background: white;
  }

  .text {
    font-size: 16px;
    font-family: MicrosoftYaHei;
  }

  .selected-text {
    color: white;
  }

  .unselected-text {
    color: #666666;
  }
</style>



