<template>
  <div>
    <button-tab :clicked="clicked" :buttons="buttons" :selectedIndex="selIndex"></button-tab>

    <component :is="curComponent"></component>
  </div>
</template>

<script>
  import ButtonTab from './member/ButtonTab.vue'
  import MemberLogin from './member/MemberLogin.vue'
  import MemberLoginDetail from './member/MemberLoginDetail.vue'
  import Members from './member/Members.vue'
  import MemberSearchCount from './member/MemberSearchCount.vue'
  import MemberSearchLogs from './member/MemberSearchLogs.vue'

    export default {
      name: '',
      components: {
        "button-tab": ButtonTab,
        "member-search-count": MemberSearchCount,
        "members": Members,
        "member-login": MemberLogin,
        "member-login-detail": MemberLoginDetail,
        "member-login-logs": MemberSearchLogs,
      },
      data() {
        return {
          buttons: [
            { index: 0, name: '成员查询量统计', componentName: 'member-search-count' },
            { index: 1, name: '成员列表', componentName: 'members' },
            { index: 2, name: '成员登录统计', componentName: 'member-login' },
            { index: 3, name: '成员登录明细', componentName: 'member-login-detail' },
            { index: 4, name: '成员查询日志', componentName: 'member-login-logs' },
          ],
          selIndex: 3,
          curComponent: '',
        }
      },
      methods: {
        clicked(data, index) {
          console.log('clicked index:' + index + " data:" + JSON.stringify(data))

          this.curComponent = data.componentName
        },
      },
      computed: {
      }
    }
</script>

<style scoped>

</style>



